* {
  
}
body {
  font-size: 18px;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #2c3e50;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main-container {
  width: 800px;
  margin: 0 auto;
  text-align: center;
  
}
.main-container h1{
  color: white;
  font-weight: bolder;
  text-align: start;
  font-size: 48px;
}

.input-container {
  width: 100%;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #34495e;
  border-radius: 5px;
}

.input-container input {
  padding-left: 20px;
  border: none;
  text-decoration: none;
  width:  100%;
  background: transparent;
  outline: none; 
  color: #bdc3c7;
  font-size: 18px;
}

.btn {
  background: transparent;
  border: none;
  font-size: 28px;
  color: #bdc3c7;
  font-size: 28px;
  cursor: pointer;
  padding-top: 5px;
  padding-right: 20px;
}
.container-list {
  padding: 0;
}

.container-list {
  padding: 0;
  width:  100%;
  display: block;
  align-items: center;
  color: #bdc3c7;
  background-color: #34495e;
  border-radius: 5px;

}

.container-list li {
  display: flex;
  align-items: center;
  padding: 10px;
}

.list-bar {
  display: flex;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 15px;
  border-bottom: 1px solid #7f8c8d
}
.list-bar .complete {
  color: #9b59b6;
}
.list-bar p {
 
  color: #3498db;
}
.list-bar p span {
  background-color: #2c3e50;
  border-radius: 5px;
  color:#95a5a6;
  padding: 6px 9px;
  
  margin-left: 10px;
}

.list-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  border-bottom: 1px solid #7f8c8d
}
.text-container {
  display: flex;
  justify-content: space-around;
  padding-left: 15px;
  text-align: start;
}
.btn-minus {
  background: transparent;
  border: none;
  display:flex;
  float: left;
  color: #bdc3c7;
  margin-right: 10px;
  cursor: pointer;
  font-size: 24px;
}

.li-info {
  width: 100%;
  box-sizing: border-box;
  font-size: 22px;
}
.li-info-date {
  height: 0;
  margin-bottom: 10px;
  font-size: 14px;
  color: #2ecc71;
}

.footer-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
}
.footer-container p {
 
  color: #7f8c8d;
}
.footer-container button {
  
  color: #7f8c8d;
  background: none;
	border: none;
	padding: 0;
	cursor: pointer;
}
.footer-container button:hover {
  color: #3498db;
}
@media only screen and (max-width: 900px) {
  .main-container {
    width: 90%;
  }
}

@media only screen and (max-width: 600px) {
  .main-container {
    width: 90%;
  }
  body {
    font-size: 14px;
  }
  .li-info {
    font-size: 16px;
  }
  .li-info-date {
    font-size: 12px;
  }
  .btn-minus {
    font-size: 20px;
  }
 
}